body {
    background-color: rgb(175, 235, 255);
    text-align: center;
    font-family: 'Arial';
}

.signUp {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: auto;
    margin-top: 200px;
    width: 90%;
    max-width: 400px;
    height: auto;
    border: 5px solid grey;
    background-color: white;
    padding: 20px;
    justify-content: space-between;
}



.profile {
    display: flex;
    flex-direction: row;
    margin-top: 3px;
}

.button {
    cursor: pointer;
    margin-left: 5px;
}

.submit {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 30px;
}

.submit_button {
    background-color: royalblue;
    color: white;
    cursor: pointer;
    border-style: none;
    border-radius: 10px;
    padding: 10px 20px;
    margin-right:20px;
}

/* Media Queries for responsiveness */
@media (max-width: 600px) {
    .signUp {
        padding: 15px;
    }

    .submit_button {
        padding: 8px 16px;
    }
}
